import '../components/countdownTimer.css'
import { useState } from 'react'
import Box from '@mui/material/Box'
import NavigateNextIcon from '@mui/icons-material/NavigateNext'
import { IconButton } from '@mui/material'
import Nav from '../pages/nav'

function CountdownTimer({duration, disabler, clickHandler, endHandler, runState}) {
  return (
    <Box sx={{ ml: "auto" }}>
      <div id="countdown">
        <svg id="timer">
          <circle
            r="15"
            cx="16"
            cy="17"
            style={{ animationDuration: duration, animationPlayState: runState ? 'running' : 'paused' }}
            onAnimationEnd={endHandler}
          ></circle>
        </svg>
        <IconButton
          aria-label="next"
          color="primary"
          sx={{position:'absolute'}}
          disabled={disabler}
          onClick={clickHandler}
        >
          <NavigateNextIcon id="button"/>
        </IconButton>
      </div>
    </Box>
  );
}

export default CountdownTimer;