import { Auth0Provider } from "@auth0/auth0-react";
import React from "react";
import { useNavigate } from "react-router-dom";
import { GAMESERVER_BACKEND_URL } from "./components/config";

export const Auth0ProviderWithNavigate = ({ children }) => {
  const navigate = useNavigate();

  const domain = process.env.REACT_APP_AUTH0_DOMAIN;
  const clientId = process.env.REACT_APP_AUTH0_CLIENT_ID;
  const redirectUri = (process.env.NODE_ENV === 'development' ?
    process.env.REACT_APP_AUTH0_DEBUG_CALLBACK :
    process.env.REACT_APP_AUTH0_PROD_CALLBACK)

  const onRedirectCallback = (appState) => {
    console.log('in RedirectCallback with appState ' + appState?.returnTo)
    navigate(appState?.returnTo || window.location.pathname, {replace: true});
  };

  if (!(domain && clientId && redirectUri)) {
    return null;
  }

  console.log('Auth0Provider redirect = ' + redirectUri)

  return (
    <Auth0Provider
      domain={domain}
      clientId={clientId}
      authorizationParams={{
        redirect_uri: redirectUri,
      }}
      onRedirectCallback={onRedirectCallback}
    >
      {children}
    </Auth0Provider>
  );
};