import React from "react"
import { Routes, Route, HashRouter } from 'react-router-dom';
import Nav from "./pages/nav";
import Home from "./pages/home";
import Story from "./pages/story";
import StoryChain from "./pages/storychain";
import Itinerary from './pages/itinerary';
import { CallbackPage } from "./pages/callback-page";
import { ThemeProvider, createTheme } from '@mui/material/styles';
import CssBaseline from '@mui/material/CssBaseline';
import { Grid } from "@mui/material";

import './App.css';
import Trivia from "./pages/trivia";

// https://zenoo.github.io/mui-theme-creator/
const myTheme = createTheme({
  palette: {
    mode: 'light',
    primary: {
      main: '#e91e63',
    },
    secondary: {
      main: '#ff9100',
    },
    background: {
      default: '#bcaaa4',
      paper: '#f3e5f5',
    },
    text: {
      primary: '#4527a0',
      secondary: '#f5741e',
      hint: '#304ffe',
    },
  },
  overrides: {
    MuiButton: {
      root: {
        background: 'linear-gradient(45deg, #FE6B8B 30%, #FF8E53 90%)',
        border: 0,
        borderRadius: 3,
        boxShadow: '0 3px 5px 2px rgba(255, 105, 135, .3)',
        fontFamily: 'Roboto',
        color: 'white',
        height: 48,
        padding: '0 30px',
      },
    },
  },
  shape: {
    borderRadius: 12,
  },
  components: {
    MuiCssBaseline: {
      styleOverrides: {
        body: {
          // backgroundImage: 'url("/parchment.svg")',
          // backgroundSize: 'cover',
          backgroundColor: '#E2F1FF',
          backgroundImage: 'url("/jupiter.svg")',
          textShadow: '1px 1px 2px white',
        }
      }
    },
    MuiPaper: {
      styleOverrides: {
        body: {
          backgroundColor: '#0000cc',  // TODO(mrisher): not working
        }
      }
    }
  }
})


function App() {
  // TODO(risher): Change HashRouter to BrowserRouter when not deploying to Github Pages
  return (
    <div className="App">
      <ThemeProvider theme={myTheme}>
        <CssBaseline />
        <Grid container>

            <Routes>
              <Route path="*" element={<Nav />}>
                <Route index element={<Home />} />
                <Route path="itinerary">
                  <Route path=":contentId?" element={<Itinerary />} />
                </Route>
                <Route path="story">
                  <Route path=":contentId?" element={<Story />} />
                </Route>
                <Route path="storychain">
                  <Route path=":contentId?" element={<StoryChain />} />
                </Route>
                <Route path="trivia">
                  <Route path=":contentID?" element={<Trivia />} />
                </Route>
                <Route path="callback" element={<CallbackPage />} />
              </Route>
            </Routes>

        </Grid>
      </ThemeProvider>
    </div>
  );
}

export default App;
