import { Outlet, Link, useOutlet} from "react-router-dom";
import * as React from 'react';
import { useState, useEffect } from "react";
import AppBar from '@mui/material/AppBar';
import { ThemeProvider, createTheme } from '@mui/material/styles';
import Box from '@mui/material/Box';
import Toolbar from '@mui/material/Toolbar';
import Typography from '@mui/material/Typography';
import IconButton from '@mui/material/IconButton';
import MenuIcon from '@mui/icons-material/Menu';
import { Grid, List, ListItemButton, ListItemText } from "@mui/material";
import Drawer from "@mui/material/Drawer";
import LoginButton from "../login";
import LogoutButton from "../logout";
import Profile from "../profile";
import { GAMESERVER_BACKEND_URL } from "../components/config";
import { useAuth0, withAuthenticationRequired } from "@auth0/auth0-react";



const myTheme = createTheme({
  palette: {
    primary: {
      main: '#e91e63',
    },
    secondary: {
      main: '#ff9100',
    },
    background: {
      default: '#bcaaa4',
      paper: '#f3e5f5',
    },
    text: {
      primary: '#4527a0',
      secondary: '#f5741e',
      hint: '#304ffe',
    },
  },
  typography: {
    fontFamily: 'Roboto',
    h4: {
      fontFamily: 'VT323',
      textShadow: '4px 4px 8px grey'
    },
  }
})

function Nav() {
  // Find the current route object
  const outlet = useOutlet();
  const [isDrawerOpen, setIsDrawerOpen] = useState(false);
  const [title, setTitle] = React.useState()
  const [score, setScore] = React.useState()
  const { user, isAuthenticated } = useAuth0();
  // TODO: Access BrowserRouter to read these from the source of truth
  const routes = ["/", "itinerary", "story", "storychain", "trivia", "index"];
  const open = true;


  useEffect(() => {
    getScore()
  }, [])

  // get the score
  function getScore() {
    let scoreURL = `${GAMESERVER_BACKEND_URL}score`
    console.log(`Fetching score from ${scoreURL}`)
    fetch(scoreURL, {
      headers: {
        Authorization: `Bearer ${user.email}`,
      },
    })
      .then(response => {
        console.log(`${scoreURL} response.status = ${response.status}`)
        if (response.ok) {
          return response.json()
        } else if (response.status === 404) {
          setScore(0)
          return    // silently, no error
        }
        throw new Error("Status code: " + response.status);
      })
      .then(data => {
        // Process the retrieved data
        if (data) {
          console.log("Fetched score... processing")
          console.log(data)
          setScore(data.score)
        }
      })
      .catch(error => {
        // Handle any errors
        console.error(`Could not retrieve score from ${scoreURL}; ${error}`)
        setScore(0)
      });
  }


  return (
    <ThemeProvider theme={myTheme}>
      <Box sx={{ flexGrow: 1 }}>
        <AppBar position="static">
          <Toolbar>
            <IconButton
              size="large"
              edge="start"
              color="inherit"
              aria-label="menu"
              onClick={() => setIsDrawerOpen(true)}
              sx={{ mr: 2 }}
            >
              <MenuIcon />
            </IconButton>
            <Grid container columns={2}>

              <Grid item xs={1}>
              <Typography variant="h4" component="div" sx={{ flexGrow: 1, textTransform: 'capitalize' }}>
              {title}
            </Typography>
              </Grid>
              <Grid item xs={1}>
              <Typography variant="h4" component="div" sx={{ textAlign: 'right' }}>
              {score}
            </Typography>
              </Grid>
            </Grid>

            <Drawer open={isDrawerOpen} onClose={() => setIsDrawerOpen(false)}>
              <List>
                <Typography variant="body2" color="secondary.main" sx={{m: 2}}><Profile /></Typography>
                {['Home', 'Itinerary', 'Story', 'Story Chain', 'Trivia', '(Future)'].map((text, index) => (
                  <ListItemButton
                    key={text}
                    component={Link} // <-- pass Link as component
                    to={routes[index]}
                    onClick={() => setIsDrawerOpen(false)}
                    sx={{
                      minHeight: 48,
                      justifyContent: open ? 'initial' : 'center',
                    }}
                  >
                    <ListItemText primary={text} sx={{ opacity: open ? 1 : 0 }} />
                  </ListItemButton>
                ))}
                <ListItemButton>
                  {isAuthenticated ? (
                    <LogoutButton></LogoutButton>
                  ): (
                    <LoginButton></LoginButton>
                  )}
                </ListItemButton>
              </List>
              <Box sx={{ position: 'absolute', bottom:0, ml:1}}>
                <Typography variant="caption" sx={{ color: 'info.main' }}>
                  &copy; 2023 - Playfully
                </Typography>
              </Box>
          </Drawer>
          </Toolbar>
        </AppBar>
        <Box sx={{p: 2}}>
          <Outlet context={{title: [title, setTitle], score: [score, setScore]}}/>
        </Box>
      </Box>
    </ThemeProvider>
  );
};

export default Nav;