import ThumbDownIcon from '@mui/icons-material/ThumbDown';
import ThumbUpIcon from '@mui/icons-material/ThumbUp';
import { Box, Divider, IconButton, MenuItem, Select, Skeleton, TextField } from "@mui/material";
import Button from '@mui/material/Button';
import CircularProgress from "@mui/material/CircularProgress";
import Link from "@mui/material/Link";
import Typography from "@mui/material/Typography";
import Grid from "@mui/material/Unstable_Grid2";
import { Recogito } from '@recogito/recogito-js';
import '@recogito/recogito-js/dist/recogito.min.css';
import React, { Fragment, useEffect, useId, useRef, useState } from "react";
import ReactAudioPlayer from "react-audio-player";
import { Link as RouterLink, useNavigate, useParams, useSearchParams } from 'react-router-dom';
import FeedbackDialog from "../components/FeedbackDialog";
import UtterancesComments from "../components/UtterancesComments";

import { GAMESERVER_BACKEND_URL } from "../components/config";


function Story() {
  const DOMId = useId()
  const [loading, setLoading] = useState(false)
  const [loaded, setLoaded] = useState(false)
  const [errMessage, setErrMessage] = useState('')
  const [story, setStory] = useState()
  const [title, setTitle] = useState('Untitled')
  const [audio, setAudio] = useState()
  const [isGeneratingAudio, setIsGeneratingAudio] = useState(false)
  const [storyId, setStoryId] = useState()
  const [characters, setCharacters] = useState("Oscar, Sacha, and a boy named Marceau")
  const [antagonist, setAntagonist] = useState("Motorcycle Monster")
  const [contagonist, setContagonist] = useState("Mister Claw, a clumsy, spooky monster with mustard-colored claws")
  const [language, setLanguage] = useState("English")
  const params = useParams()
  const [queryString] = useSearchParams()
  const navigate = useNavigate()
  const storyRef = useRef()
  const [recogito, setRecogito] = useState()
  let runOnce = true

  // if we received a content_id, bypass the form construction and retrieve that
  useEffect(() => {
    if (runOnce) {
      runOnce = false
      console.log('in the runOnce useEffect hook')
      const queryContentId = params.contentId
      if (queryContentId) {
        runOnce = false
        console.log("Query string specifies content ID " + queryContentId)
        getStoryFromContentId(queryContentId)
      } else {
        console.log("No content_id specified in URL")
      }
    }
    // eslint-disable-next-line -- empty [] intentional to make this RUNONCE
  }, [])


  // when the story gets updated (with contents from the async call)
  // then we want to finally set loaded=true
  useEffect(() => {
    console.log("in the story.useEffect hook")
    if (story) {
      setLoading(false)
      setLoaded(true)
      document.title = "GoPlayfully - " + title

      let recogitoObj = null
      if (storyRef.current) {
        recogitoObj = new Recogito({ content: storyRef.current, mode: 'pre' });

        // Add an event handler
      recogitoObj.on('createAnnotation', (annotation) => {
        console.log('annotation: ' + annotation) });
      }

      setRecogito(recogitoObj)

      // TODO(mrisher): Add storage
      // https://annotorious.github.io/guides/using-firebase-for-storage/#:~:text=var%20image%20%3D,anno%2C%20firebaseConfig%2C%20settings)%3B

      //return () => recogitoObj.destroy()
    }
  }, [story])

  function onGenerateClick() {
    setLoading(true)
    setLoaded(false)
    setErrMessage('')
    getStoryFromForm();
  }

  const feedbackCallback = (title, feedback) => {
    console.log(title);
    console.log(feedback);
  }

  function randomizeFields() {
    let tempChar = characters
    let tempAnt = antagonist
    let tempCont = contagonist

    setAntagonist(tempChar)
    setContagonist(tempAnt)
    setCharacters(tempCont)
  }

  function SkeletonRows({value}) {
    const numRows = Math.random() * 3 + 3;
    return (
      <Grid item xs={12} sx={{mb:2}}>
        {Array.apply(null, {length: numRows}).map((text, index) => (
          <Skeleton key={index} animation="wave"
            variant="text" sx={{ fontSize: '1rem' }} width={(80 + Math.random() * 20)+"%"} />
        ))}
      </Grid>
    )
  }

  // main render loop
  return (
     <Grid container spacing={2}>
        <Grid item xs={2}>
           <Select
              id="language-select"
              name="Language"
              value={language}
              disabled={loading}
              label="Language"
              onChange={(event) => setLanguage(event.target.value)}
           >
              {[
                 ["English", "English"],
                 ["Español", "Spanish"],
                 ["Français", "French"],
              ].map((text, index) => (
                 <MenuItem key={text[0]} value={text[1]}>
                    {text[0]}
                 </MenuItem>
              ))}
           </Select>
        </Grid>
        <Grid item xs={12}>
           <TextField
              required
              disabled={loading}
              fullWidth
              multiline
              id="characters"
              label="Characters"
              value={characters}
              onChange={(event) => setCharacters(event.target.value)}
           />
        </Grid>
        <Grid item xs={12}>
           <TextField
              required
              fullWidth
              disabled={loading}
              multiline
              id="antagonist"
              value={antagonist}
              label="Antagonist"
              onChange={(event) => setAntagonist(event.target.value)}
           />
        </Grid>
        <Grid item xs={12}>
           <TextField
              required
              fullWidth
              disabled={loading}
              multiline
              value={contagonist}
              id="contagonist"
              label="Contagonist"
              onChange={(event) => setContagonist(event.target.value)}
           />
        </Grid>
        <Box width="100%" />
        <Grid item xs={12}>
           <Button variant="contained" onClick={randomizeFields}>
              Randomize
           </Button>
           &nbsp;
           <Button variant="contained" onClick={onGenerateClick}>
              Go
           </Button>
        </Grid>
        <Box width="100%" />
        {story ? (
           <Fragment>
              <Grid item xs={12}>
                 <Divider>Story</Divider>
              </Grid>
              <Grid item xs={12} sx={{ minwidth: 0 }}>
                 <Typography variant="h1" sx={{ fontSize: "10vw" }}>
                    {title}
                 </Typography>
              </Grid>
              {audio ? (
                 <Grid item xs={12}>
                    <ReactAudioPlayer src={audio} autoPlay={false} controls={true} />
                 </Grid>
              ) : (
                 <Grid item xs={12}>
                    {isGeneratingAudio ? (
                       <CircularProgress />
                    ) : (
                       <Button variant="contained" color="primary" onClick={generateAudio}>
                          Generate Audio
                       </Button>
                    )}
                 </Grid>
              )}
              <Box width="100%" />
              <Grid item xs={12}>
                 <Typography
                    id={DOMId}
                    ref={storyRef}
                    variant="body1"
                    sx={{ whiteSpace: "pre-line" }}
                 >
                    {story}
                 </Typography>
              </Grid>
              <Box width="100%" />
              <Grid item xs={4}>
                 <Link component={RouterLink} to={"/story/" + storyId + "/"}>
                    permalink
                 </Link>
              </Grid>
              <Grid item xs={6}>
                 <FeedbackDialog feedbackCallback={feedbackCallback} />
              </Grid>
              <Grid item xs={1}>
                 <IconButton aria-label="thumbs up" onClick={handleThumbsUp}>
                    <ThumbUpIcon />
                 </IconButton>
              </Grid>
              <Grid item xs={1}>
                 <IconButton aria-label="thumbs down" onClick={handleThumbsDown}>
                    <ThumbDownIcon />
                 </IconButton>
              </Grid>
              <Box width="100%" />
              <Grid item xs={12}>
                 <UtterancesComments
                    repo="goplayfully/comments"
                    issueTerm={params.contentId}
                    theme={"photon-dark"}
                 />
              </Grid>
           </Fragment>
        ) : (
           <Fragment>
              {errMessage ? (
                 <Typography variant="body1" color="error.main">
                    {errMessage}
                 </Typography>
              ) : (
                 <Fragment />
              )}
           </Fragment>
        )}
        {loading ? (
           <Fragment>
              {Array.apply(null, { length: 5 }).map((text, index) => (
                 <SkeletonRows key={index} />
              ))}
           </Fragment>
        ) : (
           <Fragment />
        )}
     </Grid>
  )

  async function generateAudio() {
    setIsGeneratingAudio(true)
    try {
      const response = await fetch(`${GAMESERVER_BACKEND_URL}create_story_audio/${storyId}`, {
         method: "POST",
      })
      const data = await response.json()
      setAudio(data["audio_path"])
    } catch (error) {
      console.error("Error:", error)
    } finally {
      setIsGeneratingAudio(false)
    }
  }
  // fetch a story from the form fields
  function getStoryFromForm() {
    let storyURL = `${GAMESERVER_BACKEND_URL}story?characters=${characters}&contagonist=${contagonist}&antagonist=${antagonist}&language=${language}`
    if (queryString.get('forceFresh')) {
      storyURL += '&force_fresh=1'
      console.log('Parameter `forceFresh` is true, so overriding any cached story')
    }
    console.log(`Generating story from ${storyURL}`);
    fetch(storyURL)
      .then(response => {
        console.log(`${storyURL} response.status = ${response.status}`)
        if (response.ok) {
          return response.json()
        } else {
          throw new Error("Status code: " + response.status);
        }
      })
      .then(data => {
        // Process the retrieved data
        console.log("Fetched story... processing");
        // add timestamp to ensure a re-render in case we get exact same story (from cache)
        const d = new Date();
        setStoryId(data["content_id"])
        const slug = `\n\n(retrieved @ ${d.toISOString()})`
        setStory(data["story"] + slug)
        setTitle(data["title"])
        setAudio(data["audio_path"])
        console.log(data)
        navigate("/story/" + data["content_id"], { replace: true })
      })
      .catch(error => {
        // Handle any errors
        console.error(`Could not retrieve story from ${storyURL}; ${error}`)
        setLoading(false)
        setLoaded(false)
        setStory('')
        setTitle('Storyteller')
        setAudio('')
        setErrMessage('No story found with that ID')
      });
  }

  // fetch a story from the specified content ID in the query string
  function getStoryFromContentId(contentId) {
     setStory("")
     let storyURL = `${GAMESERVER_BACKEND_URL}story/${contentId}`
     console.log(`Fetching cached story from ${storyURL}`)
     fetch(storyURL)
        .then((response) => {
           console.log(`${storyURL} response.status = ${response.status}`)
           if (response.ok) {
              return response.json()
           } else {
              throw new Error("Status code: " + response.status)
           }
        })
        .then((data) => {
           // Process the retrieved data
           console.log("Fetched story... processing")
           // add timestamp to ensure a re-render in case we get exact same story (from cache)
           const d = new Date()
           setStoryId(data["content_id"])
           // no need to Navigate because the URL is already set
           const slug = `\n\n(retrieved @ ${d.toISOString()})`
           setStory(data["story"] + slug)
           setTitle(data["title"])
           setAudio(data["audio_path"])
           setLanguage(data["story_request"]["language"])
           setCharacters(data["story_request"]["characters"])
           setAntagonist(data["story_request"]["antagonist"])
           setContagonist(data["story_request"]["contagonist"])
           console.log(data)
        })
        .catch((error) => {
           // Handle any errors
           console.error(`Could not retrieve story from ${storyURL}; ${error}`)
           setLoading(false)
           setLoaded(false)
           setStory("")
           setAudio("")
           setErrMessage("No story found with that ID")
        })
  }
  

  // process a thumbs up (positive) feedback click
  function handleThumbsUp() {
    handleThumbs("thumbsUp")
  }

  // process a thumbs down (negative) feedback click
  function handleThumbsDown() {
    handleThumbs("thumbsDown")
  }

  // process any feedback clicks
  function handleThumbs(value) {
    console.log("Submitting feedback of " + value)
    fetch(`${GAMESERVER_BACKEND_URL}story/${storyId}`, {
      method: 'POST',
        body: JSON.stringify({
          "feedback_type": value,
          "note": "",
        }),
        headers: {
          'Content-type': 'application/json; charset=UTF-8',
        },
    })
    .then((response) => response.json())
    .then((data) => {
        console.log(data);
        // Handle data
    })
    .catch((err) => {
        console.log(err.message);
    });
  }
}

export default Story;