import React, { useState, useEffect, Fragment, useRef } from "react"
import { useOutletContext } from "react-router-dom";
import { Grid, Typography, Stack, IconButton } from "@mui/material";
import { ThemeProvider, createTheme } from '@mui/material/styles';
import {Card, CardContent, CardActions} from '@mui/material'
import NavigateNextIcon from '@mui/icons-material/NavigateNext'
import 'react-loading-skeleton/dist/skeleton.css'
import { AwesomeButton, AwesomeButtonProgress } from 'react-awesome-button';
import 'react-awesome-button/dist/styles.css';
import CountdownTimer from "../components/countdownTimer";
import { GAMESERVER_BACKEND_URL } from "../components/config";
import FeedbackButton from "../components/FeedbackButton";
import { useAuth0, withAuthenticationRequired } from "@auth0/auth0-react";


const myTheme = createTheme({
  palette: {
    info: {
      main: '#e91e63',
    },
    secondary: {
      main: '#ff9100',
    },
    background: {
      default: '#bcaaa4',
      paper: '#fff',
    },
    text: {
      primary: '#4527a0',
      secondary: '#f5741e',
      hint: '#304ffe',
    },
  },
  typography: {
    fontFamily: 'Roboto',
    h1: {
      fontFamily: 'VT323',
      textShadow: '4px 4px 8px grey'
    },
    body1: {
      fontFamily: 'Roboto',
    },
    button: {
      fontFamily: 'VT323',
    }
  },
  components: {
    MuiToggleButton : {
      styleOverrides: {
        root: {
          // "&.Mui-error": {
          //   color: "#cecece",
          //   backgroundColor: "#3668AB",
          // },
          // "&.MuiToggleButton-success &.Mui-selected": {
          //   color: "#000",
          //   backgroundColor: "#3668AB",
          //   borderColor: "#F9F871",
          //   borderWidth: "6px",
          // }
        }
      }
    }
  }
})

const Trivia = (props) => {
  // constants
  const [loading, setLoading] = useState(false)
  const [questionList, setQuestionList] = useState([])
  const [submittedAnswers, setSubmittedAnswers] = useState({})
  const [topic, setTopic] = useState('Genshin Impact')
  const [responses, setResponses] = useState({})
  const { user, isAuthenticated } = useAuth0();
  const [currentCardIndex, setCurrentCardIndex] = useState(0)
  const [banter, setBanter] = useState([])
  const [outOfQuestions, setOutOfQuestions] = useState(false)
  const [countdownTime, setCountdownTime] = useState(10)
  const {
    title: [title, setTitle],
    score: [score, setScore],
   } = useOutletContext()
   const TIMEOUT_ANSWER = -1


   // set the parent (nav) title after render
   // (cf. https://github.com/facebook/react/issues/18178)
   useEffect(() => {
    setTitle('Trivia')
   }, [])


  // store the responses and then reset all the values for the question list
  // if necessary
  function storeAndResetQuestions() {
    if (Object.keys(submittedAnswers).length) {
      postSubmittedAnswers()
    }
    setQuestionList([]);
    setSubmittedAnswers({});
    setResponses({});
    setCurrentCardIndex(0);
  }

  // called with an object {question: Q, answer: A}
  const handleTriviaAnswer = buttonId => e => {
    if (submittedAnswers[buttonId.answer] !== undefined) {
      console.log('handleTriviaAnswer called for already answered question')
      console.log('check for rerendering')
      return
    }
    setSubmittedAnswers(state => ({
      ...state,
      [buttonId.question]: buttonId.answer
    }
    )
    )
    console.log("handleTriviaAnswer() called with " + buttonId)
    let correct = false
    if (buttonId.answer === questionList[buttonId.question].correct_answer) {
      console.log("Correct: " + buttonId.answer + ", " + (isAuthenticated && user.email))
      correct = true
      addPoint()
    } else {
      console.log("Incorrect answer: " + buttonId.answer)
    }

    // only record if it was a manual answer
    if (buttonId.answer != TIMEOUT_ANSWER) {
      logResponse(questionList[buttonId.question].content_id, correct)
    }

  }

  function Question({ question, questionIndex, answers }) {
    return (
      <Stack>
        <Typography variant="h6">{question.question}</Typography>

        <Grid item container columns={2} sx={{ mt: 4 }}>
          {answers.map((answer, index) => {
            return (
              <Grid item key={index} xs={1} sx={{ mb: 2 }}>
                <AwesomeButton
                  type={"primary"}
                  key={index}
                  value={answer}
                  disabled={
                    // if there is a stored answer
                    submittedAnswers[questionIndex] ? true : false
                  }
                  onPress={handleTriviaAnswer({
                    question: questionIndex,
                    answer: answer,
                  })}
                  style={{
                    width: "90%",
                    "--button-default-border-radius": "8px",
                    "--button-primary-color": "#1E88E5",
                    "--button-primary-color-hover": "#1E88E5",
                    // button background color (green for correct, red for incorrect,
                    // grey for unpushed)
                    "--button-disabled-color":
                      answer === submittedAnswers[questionIndex]
                        ? answer === questionList[questionIndex].correct_answer
                          ? "#9AF591"
                          : "#DB577F"
                        : "#A4ABBD",
                    "--button-disabled-color-hover":
                      answer === submittedAnswers[questionIndex]
                        ? answer === questionList[questionIndex].correct_answer
                          ? "#9AF591"
                          : "#DB577F"
                        : "#A4ABBD",
                    // -light is the text color
                    "--button-disabled-color-light":
                      answer === submittedAnswers[questionIndex]
                        ? answer === questionList[questionIndex].correct_answer
                          ? "#00ACF3"
                          : "#fefefe"
                        : "#fefefe",
                    "--button-raise-level":
                      answer === submittedAnswers[questionIndex]
                        ? "1px"
                        : "4px",
                  }}
                >
                  {answer}
                </AwesomeButton>
              </Grid>
            );
          })}
        </Grid>
        <Explanation questionIndex={questionIndex}/>
      </Stack>
    );
  }


  useEffect(() => {
    // async posting of score when it changes
    if (score > 0) {
      postScore()
    }
  }, [score])


  function Explanation({questionIndex}) {
    if (submittedAnswers[questionIndex] === undefined)
      return

    const isCorrect = questionList[questionIndex].correct_answer ===
      submittedAnswers[questionIndex]

    let banterStr = ""
    const banterDb = (isCorrect ? banter.congrats : banter.trash)
    if (banter && banterDb?.length >= 1) {
      banterStr = banterDb.pop().text
    }
    else {
      banterStr = (isCorrect ? "Okay, fine." : "Nice try, Wisenheimer.")
      getBanter()
    }

    const color = isCorrect ? "success.main" : "error.main"

    if (submittedAnswers[questionIndex]) {
      return (
        <Fragment>
          <Typography
              variant="body1"
              color={color}
              sx={{mb:2, mt:2, fontWeight: 'bold'}}
            >
            { banterStr }
          </Typography>
          <Typography
              variant="body1"
              color={color}
            >
            {questionList[questionIndex].explanation}
          </Typography>
        </Fragment>
      )
    } else {
      return  // nothing
    }
  }


  function QuestionCard() {
    const [timerRunning, setTimerRunning] = useState(false)

    useEffect(() => {
      setTimerRunning(false)
    }, [currentCardIndex])

    return questionList[currentCardIndex] === undefined ? (
      ""
    ) : (
      <Card sx={{ maxWidth: 345, m: "auto" }}>
        <CardContent>
          <Question
            questionIndex={currentCardIndex}
            question={questionList[currentCardIndex]}
            answers={questionList[currentCardIndex]?.answers}
          />
        </CardContent>
        <CardActions disableSpacing>
          <FeedbackButton
            type="heart"
            callback={postHeartFeedback}
            contentId={questionList[currentCardIndex].content_id}
          />
          <FeedbackButton
            type="problem"
            callback={postProblemFeedback}
            contentId={questionList[currentCardIndex].content_id}
          />
          <CountdownTimer
            duration={"5s"}
            clickHandler={() => {
              setCurrentCardIndex(currentCardIndex + 1);
            }}
            runState={timerRunning}
            disabler={submittedAnswers[currentCardIndex] ? false : true}
            endHandler={() => {
              setTimerRunning(false);
              handleTriviaAnswer({
                question: currentCardIndex,
                answer: TIMEOUT_ANSWER,
              })
            }
          }
          />
        </CardActions>
      </Card>
    );
  }

  function addPoint() {
    console.log('Setting score to: ' + (score+1))
    setScore(score + 1)
  }

  function logResponse(contentId, correct) {
    setResponses(responses => ({
      ...responses,
      [contentId]: correct
    }))

    postSubmittedAnswers()
  }

  const feedbackCallback = (title, feedback) => {
    console.log(title);
    console.log(feedback);
  }




  return (
    <ThemeProvider theme={myTheme}>
      <Grid container spacing={2}>
        {questionList.length === 0 ? (
          <Grid item xs={12}>
            <Typography variant="body">
              Welcome to the Trivia generator. Let's get started!
            </Typography>
          </Grid>
        ) : (
          <Fragment />
        )}
        <Grid item xs={12}>
          {currentCardIndex < questionList.length ? (
            <QuestionCard />
          ) : (
            <Card sx={{ m: 2 }}>
              <CardContent sx={{ textAlign: "center" }}>
                <Typography variant="h6">
                  {Object.keys(submittedAnswers).length
                    ? "Keep it coming (another batch)?"
                    : ""}
                </Typography>
                {outOfQuestions ? (
                  <Typography variant="body1" color="error.main">
                    That's all the questions we have for now. Come back soon!
                  </Typography>
                ) : (
                  <FetchQuestionsButton />
                )}
              </CardContent>
            </Card>
          )}
        </Grid>
      </Grid>
    </ThemeProvider>
  );

  function FetchQuestionsButton() {
    return (
      <AwesomeButtonProgress
        type="primary"
        onPress={(event, next) => {
          console.log('Clicked')
          getQuestionList(next)
        }}
        style={{ width: "120px" }}
      >
        Go!
      </AwesomeButtonProgress>
    )
  }

  // fetch list of stories
  function getQuestionList(finishedCallback) {
    setLoading(true)
    storeAndResetQuestions();
    let triviaURL = `${GAMESERVER_BACKEND_URL}trivia?topic=${topic}`
    console.log(`Fetching trivia from ${triviaURL}`);
    fetch(triviaURL, {
      headers: {
        Authorization: `Bearer ${user?.email}`,
      },
    })
      .then(response => {
        console.log(`${triviaURL} response.status = ${response.status}`)
        if (response.ok) {
          return response.json()
        } else {
          finishedCallback()
          throw new Error("Status code: " + response.status);
        }
      })
      .then(data => {
        // Process the retrieved data
        let questions = data
        console.log("Fetched trivia question list... processing")
        console.log(questions)
        if (questions.length === 0) {
          setOutOfQuestions(true)
          finishedCallback()
          return
        }
        setQuestionList(questions)
        getScore()
        finishedCallback()
      })
      .catch(error => {
        // Handle any errors
        console.error(`Could not retrieve trivia from ${triviaURL}; ${error}`)
        finishedCallback()
      });
  }


  // get the score
  function getScore() {
    let scoreURL = `${GAMESERVER_BACKEND_URL}score`
    console.log(`Fetching score from ${scoreURL}`)
    fetch(scoreURL, {
      headers: {
        Authorization: `Bearer ${user.email}`,
      },
    })
      .then(response => {
        console.log(`${scoreURL} response.status = ${response.status}`)
        if (response.ok) {
          return response.json()
        } else if (response.status === 404) {
          setScore(0)
          return    // silently, no error
        }
        throw new Error("Status code: " + response.status);
      })
      .then(data => {
        // Process the retrieved data
        if (data) {
          console.log("Fetched score... processing")
          console.log(data)
          setScore(data.score)
        }
      })
      .catch(error => {
        // Handle any errors
        console.error(`Could not retrieve score from ${scoreURL}; ${error}`)
        setScore(0)
      });
  }

  function getBanter() {
    let banterURL = `${GAMESERVER_BACKEND_URL}banter`
    console.log(`Fetching banter from ${banterURL}`)
    fetch(banterURL, {
      headers: {
        Authorization: `Bearer ${user.email}`,
      },
    })
      .then(response => {
        console.log(`${banterURL} response.status = ${response.status}`)
        if (response.ok) {
          return response.json()
        } else if (response.status === 404) {
          return    // silently, no error
        }
        throw new Error("Status code: " + response.status);
      })
      .then(data => {
        // Process the retrieved data
        if (data) {
          console.log("Fetched banter... processing")
          console.log(data)
          setBanter(data)
        }
      })
      .catch(error => {
        // Handle any errors
        console.error(`Could not retrieve banter from ${banterURL}; ${error}`)
      });
  }

  // post the score
  function postScore() {
    let scoreURL = `${GAMESERVER_BACKEND_URL}score`
    console.log(`Posting score ${score} from ${scoreURL}`)
    fetch(scoreURL, {
      headers: {
        Authorization: `Bearer ${user.email}`,
        "Content-Type": "application/json",
      },
      method: "POST",
      body: JSON.stringify({'score':score})
    })
    .then(response => {
      console.log(`${scoreURL} response.status = ${response.status}`)
      return response.json()
    })
  }


  // send feedback on a question
  function postHeartFeedback(contentId) {
    postFeedback(contentId, "heart")
  }

  function postProblemFeedback(contentId) {
    postFeedback(contentId, "problem")
  }

  function postFeedback(contentId, feedback, notes) {
    let feedbackURL = `${GAMESERVER_BACKEND_URL}trivia/${contentId}/feedback`
    console.log(`Posting feedback ${feedback} from ${feedbackURL}`)
    fetch(feedbackURL, {
      headers: {
        Authorization: `Bearer ${user.email}`,
        "Content-Type": "application/json",
      },
      method: "POST",
      body: JSON.stringify({'type':feedback, 'notes':notes, 'question':questionList[contentId]})
    })
    .then(response => {
      console.log(`${feedbackURL} response.status = ${response.status}`)
      return response.json()
    })
    .catch(error => {
      console.error('Could not submit feedback' + error)
    })
  }


  // store the submitted answers on the server so they're not asked again
  function postSubmittedAnswers() {
    let submissionsURL = `${GAMESERVER_BACKEND_URL}trivia/responses`
    console.log(`Posting responses ${submissionsURL}`)
    fetch(submissionsURL, {
      headers: {
        Authorization: `Bearer ${user.email}`,
        "Content-Type": "application/json",
      },
      method: "POST",
      body: JSON.stringify(responses)
    })
    .then(response => {
      console.log(`${submissionsURL} response.status = ${response.status}`)
      return response.json()
    })
    .catch(error => {
      console.error('Could not submit responses' + error)
    })
  }
};


//export default Trivia

export default withAuthenticationRequired(Trivia, {
  returnTo: '/trivia/'
});
